/* Theme toggle */
.switch {
  display: inline-block;
  height: 18px;
  justify-self: end;
  position: relative;
  width: 36px;
  
  input {
    opacity: 0;
    width: 0;
    height: 0;

    &:checked + .slider {
      background-color: var(--mainFormBgColor);
    }
    
    &:focus + .slider {
      box-shadow: 0 0 1px var(--mainFormBgColor);
    }
    
    &:checked + .slider:before {
      -webkit-transform: translateX(18px);
      -ms-transform: translateX(18px);
      transform: translateX(18px);
    }
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--currentButtonColor);
    -webkit-transition: .2s;
    transition: .2s;

    &:before {
      position: absolute;
      content: "";
      height: 18px;
      width: 18px;
      left: 0;
      bottom: 0;
      background-color: var(--textColor);
      -webkit-transition: .2s;
      transition: .2s;
    }

    &.round {
      border-radius: 34px;
    
      &:before {
        border-radius: 50%;
        content: var(--themeIcon);
        line-height: 20px;
        text-align: center;
      }
    }
  }
}

